import React, {Component, Fragment } from 'react';
import Chart from "chart.js";

let temperatureChart = null;

class TemperatureWrapper extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTemperature: 'tavg',
            gradient: ['rgba(250,174,50,1)','rgba(250,174,50,0)'],
            gradientLinearity: [0, 0, 0, 150]
        }
    }

    updateSelection(newTemperature, gradient, gradientLinearity) {
        this.setState(state => ({
            selectedTemperature: newTemperature,
            gradient: gradient,
            gradientLinearity: gradientLinearity
        }));
    }

    render() {
        return(
            <div className="card rounded-0">
                <div className="card-body">
                    <div className="row">
                        <div className="col-5">
                            <h5 className="card-title">Yearly Temperature</h5>
                        </div>
                        <div className="col-7">
                            <ul className="nav nav-pills card-header-pills float-right mr-3">
                                <li className="nav-item">
                                    <button type="button" className="btn btn-light" onClick={() => {this.updateSelection('tavg',['rgba(250,174,50,1)','rgba(250,174,50,0)'],[0, 0, 0, 150])}}>
                                        Avg
                                    </button>
                                </li>
                                <li className="nav-item mx-1">
                                    <button type="button" className="btn btn-light" onClick={() => {this.updateSelection('tmax',['rgba(249,107,46,1)','rgba(250,174,50,0.45)'],[0, 0, 0, 150])}}>
                                        Max
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button type="button" className="btn btn-light" onClick={() => {this.updateSelection('tmin',['rgba(50,178,250,1)','rgba(185,46,249,0.1)'],[0, 0, 0, 300])}}>
                                        Min
                                    </button>
                                </li>
                            </ul>    
                        </div>
                    </div>
                    <div>
                        <Temperature id={this.state.selectedTemperature} data={this.props.data} gradient={this.state.gradient} gradientLinearity={this.state.gradientLinearity}/>
                    </div>
                </div>
            </div>
        );
    }
}

class Temperature extends Component {
    constructor(props) {
        super(props);
        this.state = {
            type: this.props.type,
            id: this.props.id,
        }
    }

    componentDidMount() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
    
    }
    
    componentDidUpdate() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
        
    }

    isCanvasBlank(canvas) {
        return !canvas.getContext('2d')
          .getImageData(0, 0, canvas.width, canvas.height).data
          .some(channel => channel !== 0);
    }

    createGraph() {
        /** Clear canvas before redrawing */
        if (temperatureChart !== null) temperatureChart.destroy();

        //Remove the label from the chart
        Chart.defaults.global.legend.display = false;

        /*** Gradient ***/
        let averageTemperatureChartElement = document.getElementById(this.props.id);
        let ctxAvT = averageTemperatureChartElement.getContext("2d");

        let gradientAvT = ctxAvT.createLinearGradient(this.props.gradientLinearity[0], this.props.gradientLinearity[1], this.props.gradientLinearity[2], this.props.gradientLinearity[3]);
        gradientAvT.addColorStop(0, this.props.gradient[0]);   
        gradientAvT.addColorStop(1, this.props.gradient[1]);

        
        temperatureChart = new Chart(averageTemperatureChartElement, {
            type: "line",
            data: {
                //Bring in data
                labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
                datasets: [
                    {
                        label: "Average Temperature",
                        fillColor : gradientAvT,
                        strokeColor : "#ff6c23",
                        pointColor : "#fff",
                        pointStrokeColor : "#ff6c23",
                        pointHighlightFill: "#fff",
                        pointHighlightStroke: "#ff6c23",
                        backgroundColor: gradientAvT,
                        data: [
                        this.props.data.data[0][this.props.id], 
                        this.props.data.data[1][this.props.id],
                        this.props.data.data[2][this.props.id], 
                        this.props.data.data[3][this.props.id],
                        this.props.data.data[4][this.props.id], 
                        this.props.data.data[5][this.props.id], 
                        this.props.data.data[6][this.props.id], 
                        this.props.data.data[7][this.props.id],
                        this.props.data.data[8][this.props.id], 
                        this.props.data.data[9][this.props.id], 
                        this.props.data.data[10][this.props.id], 
                        this.props.data.data[11][this.props.id]
                        ],
                        fill: 'start'
                    }
                ]
            },
            options: {
                scales: {
                yAxes: [
                    {
                    ticks: {
                        display: true //this will remove only the label
                    },
                    gridLines: {
                        drawBorder: false
                    }
                    }
                ],
                xAxes: [
                    {
                    gridLines: {
                        drawBorder: false
                    }
                    }
                ]
                },
                layout: {
                padding: {
                    left: -5,
                }
                } 
            }
        });   
    }

    render() {
        return (
            <Fragment>
                <canvas id={this.props.id} className="temperatureChart"/>  
            </Fragment>
        );
    }
}

export default TemperatureWrapper;