import React, {Component, Fragment } from 'react';
import Chart from "chart.js";

let precipitationChart = null;

class Precipitation extends Component {
    componentDidMount() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
    
    }
    
    componentDidUpdate() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
        
    }

    createGraph() {
        /** Clear canvas before redrawing */
        if (precipitationChart !== null) precipitationChart.destroy();

        Chart.defaults.global.legend.display = false;

        precipitationChart = new Chart(document.getElementById(this.props.id), {
            type: "bar",
            data: {
                //Bring in data
                labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
                datasets: [
                    {
                        label: "Average precipitation",
                        borderColor: 'rgba(75, 192, 192, 1)',
                        backgroundColor: 'rgba(75, 192, 192, 0.2)',
                        data: [
                            this.props.data.data[0][this.props.id], 
                            this.props.data.data[1][this.props.id],
                            this.props.data.data[2][this.props.id], 
                            this.props.data.data[3][this.props.id],
                            this.props.data.data[4][this.props.id], 
                            this.props.data.data[5][this.props.id], 
                            this.props.data.data[6][this.props.id], 
                            this.props.data.data[7][this.props.id],
                            this.props.data.data[8][this.props.id], 
                            this.props.data.data[9][this.props.id], 
                            this.props.data.data[10][this.props.id], 
                            this.props.data.data[11][this.props.id]
                        ]
                    }
                ]
            },
            options: {
                layout: {
                    padding: {
                        left: -5,
                    }
                },
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: true
                        }
                    }]
                }
            }
        });
        
    }

    render() {
        return (
            <Fragment>
                <canvas id={this.props.id}/>
            </Fragment>
        );
    }
}

export default Precipitation;